import { css } from 'glamor'

const style = {
  wrap: css({
    position: 'relative',
    width: '100%',
  }),
  wrapDark: css({
    background: '#2b2c30',
    position: 'relative',
    width: '100%',
  }),
  header: css({
    background: 'linear-gradient(90deg, #ff9900, #ffbe00)',
    color: '#fff',
    fontSize: '1.6rem',
    height: '9rem',
    justifyContent: 'center',
    lineHeight: '9rem',
    letterSpacing: '0.1em',
    margin: 0,
    padding: '0 4rem',
    textAlign: 'center',
    textTransform: 'uppercase',
    width: '100%',
  }),
  thumbs: css({
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'row',
    margin: 'auto',
    maxWidth: '136.6rem',
    padding: '6rem',
  }),
  thumb: css({
    backgroundPosition: 'center',
    border: '10px solid #D8D8D8',
    height: '0',
    margin: 0,
    paddingTop: '100%',
    position: 'relative',
    transition: 'all 0.7s',
    width: '100%',
    '& > figcaption': {
      alignItems: 'center',
      background: 'rgba(0,0,0,0.9)',
      bottom: 0,
      color: '#fff',
      content: '""',
      display: 'flex',
      flexDirection: 'column',
      fontSize: '2rem',
      fontWeight: 900,
      justifyContent: 'center',
      left: 0,
      opacity: 0,
      position: 'absolute',
      right: 0,
      top: 0,
      transition: 'all 0.7s',
      textAlign: 'center',
      textTransform: 'uppercase',
      '& > p': {
        display: 'block',
        margin: 0,
      },
      '& > span': {
        color: '#FF9900',
        display: 'block',
        fontFamily: 'Milkshake',
        fontSize: '1.8rem',
        textTransform: 'capitalize',
      },
    },
    ':hover': {
      borderColor: '#FF9900',
    },
    ':hover > figcaption': {
      opacity: 1,
    },
  }),
  link: css(
    {
      padding: '2rem 0',
      width: '100%',
    },
    {
      '@media(min-width: 768px)': {
        padding: '2rem',
        width: '50%',
      },
    },
    {
      '@media(min-width: 1024px)': {
        padding: '2rem',
        width: '33.3%',
      },
    }
  ),
  description: css(
    {
      color: '#989EA8',
      fontSize: '1.6rem',
      lineHeight: 1.8,
      marginTop: '4rem',
      maxWidth: '100%',
      padding: '0 4rem',
    },
    {
      '@media(min-width: 768px)': {
        padding: '0 18rem',
      },
    },
    {
      '@media(min-width: 1336px)': {
        columnCount: 2,
        columnGap: '2.7vw',
        maxWidth: '110rem',
        padding: '0 8rem',
        width: '65%',
      },
    }
  ),
  imgs: css(
    {
      margin: '-4rem auto 0',
      maxWidth: '100%',
      overflow: 'visible',
      padding: '8rem',
    },
    {
      '@media(min-width: 768px)': {
        maxWidth: '110rem',
        padding: '0 8rem 12rem',
        width: '100%',
      },
      '@media(min-width: 1024px)': {
        margin: '-35vh auto 0',
      },
    }
  ),
  imgWrap: css(
    {
      boxShadow: '0 10px 40px rgba(0,0,0,0.5)',
      display: 'block',
      margin: '2rem 0 0 0',
      position: 'relative',
      width: '100%',
    },
    {
      '@media(min-width: 1336px)': {
        marginTop: '6rem',
      },
    }
  ),
}

export default style



