import { css } from 'glamor'
import imgHero from '../img/bg_home.png'
import imgBg from '../img/bg_footer.png'

const style = {
  wrap: css(
    {
      background: `#2B2D30`,
      position: 'relative',
      width: '100%',
    },
    {
      '@media(min-width: 1024px)': {
        background: `#2B2D30 url(${imgBg}) no-repeat center left`,
        backgroundSize: 'contain',
      },
    }
  ),
  content: css({
    maxWidth: '136.6rem',
    margin: 'auto',
    padding: '9rem 8rem',
    width: '100%',
  }),
  title: css({
    color: '#FF9900',
    fontFamily: 'Milkshake',
    fontSize: '2.4rem',
    fontWeight: 'normal',
    margin: 0,
    ':after': {
      background: '#FF9900',
      content: '""',
      display: 'block',
      height: '0.2rem',
      marginTop: '3rem',
      width: '5rem',
    },
  }),
  copy: css({
    color: '#989EA8',
    fontSize: '1.6rem',
    lineHeight: 1.8,
    maxWidth: '80rem',
  }),
  copyright: css(
    {
      color: '#989EA8',
      fontSize: '1.2rem',
      marginTop: '13rem',
      textAlign: 'right',
    },
    {
      '@media(min-width: 768px)': {
        textAlign: 'left',
        width: '50%',
      },
    }
  ),
  social: css(
    {
      display: 'flex',
      flexDirection: 'row',
      height: '4.5rem',
      listStyle: 'none',
      margin: '0 0 0 auto',
      padding: 0,
      textAlign: 'right',
      width: '100%',
      '& li': css({
        overflow: 'hidden',
        height: '4.5rem',
        margin: '0 0 0 2rem',
        transition: 'all 0.3s',
        width: '4.5rem',
        ':first-child': {
          margin: '0 0 0 auto',
        },
        ':hover': css({
          opacity: '0.6',
        }),
      }),
    },
    {
      '@media(min-width: 768px)': {
        margin: '-5.5rem 0 0 auto',
        width: '50%',
      },
    }
  ),
}

export default style



