import imgGlacier from '../img/bg-glacier.jpg'
import imgBrave from '../img/bg-bravelydefault.jpg'
import imgIysl from '../img/bg-condom.jpg'
import imgJungle from '../img/bg-jungle.jpg'
import imgMario from '../img/bg-mario.jpg'
import imgSpace from '../img/bg-space.png'
import imgTfc from '../img/bg-tfc.png'
import imgCutty from '../img/bg-cutty.png'
import imgNissan from '../img/bg-nissan.png'
import imgEvocalize from '../img/bg-evocalize.png'
import imgWoods from '../img/bg-woods.jpg'
import imgClub from '../img/bg-club.png'
import { css, media } from 'glamor'

const style = {
  bgOSS: css({
    background: `#091f33 url(${imgGlacier}) no-repeat center left`,
    backgroundSize: 'cover',
  }),
  bgEta: css({
    background: `#091f33 url(${imgSpace}) no-repeat center left`,
    backgroundSize: 'cover',
  }),
  bgIRC: css({
    background: `#091f33 url(${imgJungle}) no-repeat center left`,
    backgroundSize: 'cover',
  }),
  bgBrave: css({
    background: `#091f33 url(${imgBrave}) no-repeat center left`,
    backgroundSize: 'cover',
  }),
  bgIysl: css({
    background: `#091f33 url(${imgIysl}) no-repeat center left`,
    backgroundSize: 'cover',
  }),
  bgMario: css({
    background: `#091f33 url(${imgMario}) no-repeat center left`,
    backgroundSize: 'cover',
  }),
  bgTfc: css({
    background: `#091f33 url(${imgTfc}) no-repeat center left`,
    backgroundSize: 'cover',
  }),
  bgCutty: css({
    background: `#091f33 url(${imgCutty}) no-repeat center`,
    backgroundSize: 'cover',
  }),
  bgNissan: css({
    background: `#091f33 url(${imgNissan}) no-repeat center`,
    backgroundSize: 'cover',
  }),
  bgEvocalize: css({
    background: `#091f33 url(${imgEvocalize}) no-repeat center`,
    backgroundSize: 'cover',
  }),
  bgWoods: css({
    background: `#091f33 url(${imgWoods}) no-repeat center`,
    backgroundSize: 'cover',
  }),
  bgClub: css({
    background: `#091f33 url(${imgClub}) no-repeat center`,
    backgroundSize: 'cover',
  }),
}

export default style



