import React, { Component, PropTypes } from 'react'
import Link, { navigateTo } from 'gatsby-link'
import classNames from 'classnames'
import img1 from '../img/icon_pinterest_gradient.svg'
import img2 from '../img/icon_facebook_gradient.svg'
import img3 from '../img/icon_twitter_gradient.svg'
import smoothScroll from 'smoothscroll'
import { css } from 'glamor'

class Navigation extends Component {
  constructor(props) {
    super(props)
    this.state = {
      navOpen: false,
    }
  }

  handleClickNav(num, event) {
    event.preventDefault()
    const { navOpen } = this.state
    const destination = {
      0: document.getElementById('jump-nav-0'),
      1: document.getElementById('jump-nav-1'),
      2: document.getElementById('jump-nav-2'),
    }
    if (destination[num]) {
      smoothScroll(destination[num])
      this.setState({ navOpen: false })
    } else {
      navigateTo('/')
      setTimeout(() => {
        const destination = {
          0: document.getElementById('jump-nav-0'),
          1: document.getElementById('jump-nav-1'),
          2: document.getElementById('jump-nav-2'),
        }
        smoothScroll(destination[num])
        this.setState({ navOpen: false })
      }, 500)
    }
  }

  handleClick() {
    const { navOpen } = this.state
    this.setState({ navOpen: !navOpen })
  }

  render() {
    const { navOpen } = this.state

    return (
      <div id="menu">
        <nav
          id="nav"
          className={navOpen && 'open'}
          onClick={() => this.handleClick()}
        >
          <span />
          <span />
          <span />
          <span />
        </nav>
        <div id="overlay" className={navOpen && 'open'} />
        <ul className={classNames({ menu: true, open: navOpen })}>
          <li>
            <a href="#" onClick={e => this.handleClickNav('0', e)}>
              <span className="navCategory">Welcome</span>
              <span className="navTitle">Home</span>
            </a>
          </li>
          <li>
            <a href="#" onClick={e => this.handleClickNav('1', e)}>
              <span className="navCategory">Work</span>
              <span className="navTitle">Engineering</span>
            </a>
          </li>
          <li>
            <a href="#" onClick={e => this.handleClickNav('2', e)}>
              <span className="navCategory">Work</span>
              <span className="navTitle">Design+</span>
            </a>
          </li>
        </ul>
        <ul
          className={classNames({
            menuSocial: true,
            open: navOpen,
          })}
        >
          <li>
            <a
              href="https://www.pinterest.com/jonathandcooper/"
              target="_blank"
            >
              <img src={img1} />
            </a>
          </li>
          <li>
            <a
              href="https://www.facebook.com/jonathan.cooper.9887117"
              target="_blank"
            >
              <img src={img2} />
            </a>
          </li>
          <li>
            <a href="https://twitter.com/JonathanCooperD" target="_blank">
              <img src={img3} />
            </a>
          </li>
        </ul>
      </div>
    )
  }
}

export default Navigation



