import { css } from 'glamor'
import imgHero from '../img/bg_home.png'

const style = {
  header: css(
    {
      alignItems: 'center',
      background: `#252629 url(${imgHero}) no-repeat center`,
      backgroundSize: 'cover',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      minHeight: '40rem',
      padding: '8rem 4rem',
      width: '100%',
    },
    {
      '@media(min-width: 1024px)': {
        minHeight: 'calc(100vh - 9rem)',
      },
    }
  ),
  headerTitle: css({
    color: 'white',
    fontWeight: 900,
    fontSize: '10vw',
    margin: 0,
    textAlign: 'center',
    width: '100%',
  }),
  headerTitleSmall: css({
    color: 'white',
    fontWeight: 900,
    fontSize: '7vw',
    margin: 0,
    textAlign: 'center',
    width: '100%',
  }),
  headerSubTitle: css(
    {
      color: '#FF9900',
      display: 'block',
      fontSize: '1.8rem',
      fontFamily: 'Milkshake',
      margin: '0',
      textAlign: 'center',
      width: '100%',
    },
    {
      '@media(min-width: 1024px)': {
        fontSize: '4rem',
      },
    }
  ),
  button: css(
    {
      background: 'linear-gradient(90deg, #ff9900, #ffbe00)',
      border: 'none',
      borderRadius: '3.5rem',
      boxShadow: '0 4px 0 #E18700',
      color: '#fff',
      cursor: 'pointer',
      display: 'block',
      fontSize: '1.6rem',
      fontWeight: '700',
      height: '7rem',
      letterSpacing: '0.1em',
      lineHeight: '7rem',
      marginTop: '3rem',
      maxWidth: '28rem',
      outline: 'none',
      textAlign: 'center',
      textTransform: 'uppercase',
      textDecoration: 'none',
      width: '100%',
      ':hover': {
        boxShadow: '0 6px 0 #E18700',
        background: 'linear-gradient(0deg, #ff9900, #ffbe00)',
        transform: 'translateY(-0.2rem)',
      },
      ':active': {
        boxShadow: '0 2px 0 #E18700',
        background: 'linear-gradient(90deg, #ff9900, #ffbe00)',
        transform: 'translateY(0.2rem)',
      },
    },
    {
      '@media(min-width: 1024px)': {
        marginTop: '8rem',
      },
    }
  ),
}

export default style



