import React, { PropTypes } from 'react'
import Link from 'gatsby-link'
import img1 from '../img/icon_pinterest.svg'
import img2 from '../img/icon_facebook.svg'
import img3 from '../img/icon_twitter.svg'
import footer from '../style/footer'
import { css } from 'glamor'

const Footer = () => (
  <footer {...footer.wrap}>
    <div {...footer.content}>
      <h3 {...footer.title}>About Me</h3>
      <p {...footer.copy}>
        Hi, Im Jonathan Cooper, a UI Engineer from Austin, TX. I design and
        develop applications, helping clients solve problems and produce high
        quality software in a modern web.
      </p>
      <p {...footer.copyright}> 2018 Jonathan Cooper</p>
      <ul {...footer.social}>
        <li>
          <a href="https://www.pinterest.com/jonathandcooper/" target="_blank">
            <img src={img1} />
          </a>
        </li>
        <li>
          <a
            href="https://www.facebook.com/jonathan.cooper.9887117"
            target="_blank"
          >
            <img src={img2} />
          </a>
        </li>
        <li>
          <a href="https://twitter.com/JonathanCooperD" target="_blank">
            <img src={img3} />
          </a>
        </li>
      </ul>
    </div>
  </footer>
)

PropTypes.Footer = {}

export default Footer



